import {Action} from '@ngrx/store';
import {TreksModel} from '../../roles/business/treks/treks.model';
import {TrekPersonDetailsModel} from '../treks/trekPersonDetails.model';
import {EventPersonDetailsModel} from '../events/eventPersonDetails.model';

export const TRY_FETCH_TREK = 'TRY_FETCH_TREK';
export const SET_ALL_TREK = 'SET_ALL_TREK';
export const SET_ACTIVE_TREK = 'SET_ACTIVE_TREK';
export const SET_SELECTED_TREK_DETAILS = 'SET_SELECTED_TREK_DETAILS';

export const TRY_FETCH_EVENT = 'TRY_FETCH_EVENT';
export const SET_ALL_EVENT = 'SET_ALL_EVENT';
export const SET_ACTIVE_EVENT = 'SET_ACTIVE_EVENT';
export const SET_SELECTED_EVENT_DETAILS = 'SET_SELECTED_EVENT_DETAILS';

export const SET_PRICE = 'SET_PRICE';


export class TryFetchEvent implements Action {
  readonly type = TRY_FETCH_EVENT;

  constructor(
    public payload: { _id: string }
  ) {
  }
}

export class SetActiveEvent implements Action {
  readonly type = SET_ACTIVE_EVENT;

  constructor(public payload: TreksModel) {
  }
}

export class SetAllEvent implements Action {
  readonly type = SET_ALL_EVENT;

  constructor(public payload: any) {
  }
}


export class SetActiveEventDetails implements Action {
  readonly type = SET_SELECTED_EVENT_DETAILS;

  constructor(public payload: EventPersonDetailsModel) {
  }

}


export class SetAllTrek implements Action {
  readonly type = SET_ALL_TREK;

  constructor(public payload: any) {
  }
}

export class SetActiveTrek implements Action {
  readonly type = SET_ACTIVE_TREK;

  constructor(public payload: TreksModel) {
  }
}

export class TryFetchTrek implements Action {
  readonly type = TRY_FETCH_TREK;

  constructor(
    public payload: { _id: string }
  ) {
  }
}


export class SetPrice implements Action {
  readonly type = SET_PRICE;

  constructor(public payload: number) {
  }
}

export class SetActiveTrekDetails implements Action {
  readonly type = SET_SELECTED_TREK_DETAILS;

  constructor(public payload: TrekPersonDetailsModel) {
  }

}

export type DemandActions =
  SetAllEvent | SetActiveEvent | TryFetchEvent |
  SetActiveEventDetails |
  SetActiveTrekDetails |
  SetPrice |
  TryFetchTrek |
  SetActiveTrek |
  SetAllTrek;
